import { createClient } from '@supabase/supabase-js'


const supabaseUrl = process.env.REACT_APP_SUPABASE_URL
const supabaseKey = process.env.REACT_APP_SUPABASE_KEY

if (!supabaseUrl || !supabaseKey) {
  throw new Error('Supabase   .');
}

const supabase = createClient(supabaseUrl, supabaseKey, {
  auth: {
    autoRefreshToken: true,
    persistSession: true,
    detectSessionInUrl: true,
    storageKey: 'blog-auth-token',
    storage: window.localStorage,
    // 60   
    expiryMargin: 60 * 60
  }
});

export const blogApi = {
  //   API
  async getPosts(page = 0) {
    try {
      const from = page * 5;
      const to = from + 4;
      
      //   
      const { data: posts, error: postsError, count } = await supabase
        .from('posts')
        .select('*', { count: 'exact' })
        .range(from, to)
        .order('created_at', { ascending: false });
      
      if (postsError) throw postsError;

      console.log('  :', posts); //   

      //     
      const postsWithAuthors = await Promise.all(
        posts.map(async (post) => {
          if (!post.author_id) return post;

          const { data: authorData } = await supabase
            .from('profiles')
            .select('name')
            .eq('id', post.author_id)
            .single();

          console.log(' :', authorData); //   

          return {
            ...post,
            author: authorData?.name
          };
        })
      );

      //  
      const formattedData = postsWithAuthors.map(post => {
        const formatted = {
          id: post.id,
          title: post.title,
          content: post.content,
          excerpt: post.excerpt || (post.content ? post.content.substring(0, 150) + '...' : ''),
          image: post.image_url,
          createdAt: new Date(post.created_at).toLocaleDateString('ko-KR', {
            year: 'numeric',
            month: 'long',
            day: 'numeric'
          }),
          author: post.author || '',
          likes: post.likes || 0,
          dislikes: post.dislikes || 0
        };
        console.log(' :', formatted); //   
        return formatted;
      });
      
      return {
        content: formattedData,
        totalPages: Math.ceil(count / 5)
      };
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  },

  createPost: (postData) => supabase.from('posts').insert(postData),
  
  //   API
  auth: {
    // 
    async signUp(email, password) {
      const { data, error } = await supabase.auth.signUp({
        email,
        password,
      });
      if (error) throw error;
      return data;
    },

    // 
    async signIn(email, password) {
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password,
      });
      if (error) throw error;
      return data;
    },

    //   
    async getCurrentUser() {
      const { data: { user }, error } = await supabase.auth.getUser();
      
      if (error) {
        console.error('getCurrentUser :', error);
        return null;
      }

      //   
      const session = await supabase.auth.getSession();
      if (session?.data?.session?.expires_at) {
        const expiresAt = new Date(session.data.session.expires_at * 1000);
        if (expiresAt < new Date()) {
          await this.signOut();
          return null;
        }
      }

      return user;
    },

    // 
    async signOut() {
      const { error } = await supabase.auth.signOut();
      if (error) throw error;
    }
  },

  //   API
  async getProfile() {
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) throw new Error(' ');

    const { data, error } = await supabase
      .from('profiles')
      .select('*')
      .eq('id', user.id)
      .single();
    
    if (error) throw error;
    return data;
  },

  async updateProfile(profileData) {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) throw new Error(' ');

      //   URL 
      let avatarUrl = profileData.avatar_url;
      const imageFile = profileData.imageFile;

      //       
      if (imageFile) {
        try {
          const fileExt = imageFile.name.split('.').pop();
          const fileName = `${Date.now()}.${fileExt}`;
          const filePath = `public/${fileName}`;

          const { error: uploadError } = await supabase.storage
            .from('profile_images')
            .upload(filePath, imageFile);

          if (uploadError) throw uploadError;

          const { data } = supabase.storage
            .from('profile_images')
            .getPublicUrl(filePath);

          avatarUrl = data.publicUrl;
        } catch (error) {
          console.error('  :', error);
          //      URL 
        }
      }

      //   
      const { error } = await supabase
        .from('profiles')
        .upsert({
          id: user.id,
          name: profileData.name,
          bio: profileData.bio,
          github_url: profileData.github_url,
          linkedin_url: profileData.linkedin_url,
          avatar_url: avatarUrl,  //     URL 
          updated_at: new Date()
        });

      if (error) throw error;

      return {
        ...profileData,
        avatar_url: avatarUrl
      };
    } catch (error) {
      throw error;
    }
  },
  
  // /  API
  async updateReaction(postId, type) {
    const { error } = await supabase.rpc(
      type === 'like' ? 'increment_likes' : 'increment_dislikes',
      { post_id: postId }
    );
    
    if (error) throw error;
  },

  async getPost(id) {
    try {
      //    
      const { data: post, error: postError } = await supabase
        .from('posts')
        .select('*')
        .eq('id', id)
        .single();
      
      if (postError) throw postError;

      //   
      let authorName = '';
      if (post.author_id) {
        const { data: authorData } = await supabase
          .from('profiles')
          .select('name')
          .eq('id', post.author_id)
          .single();
        
        if (authorData) {
          authorName = authorData.name;
        }
      }

      //  
      return {
        id: post.id,
        title: post.title,
        content: post.content,
        excerpt: post.excerpt,
        image: post.image_url,
        createdAt: new Date(post.created_at).toLocaleDateString('ko-KR', {
          year: 'numeric',
          month: 'long',
          day: 'numeric'
        }),
        author: authorName,
        author_id: post.author_id,
        likes: post.likes || 0,
        dislikes: post.dislikes || 0
      };
    } catch (error) {
      console.error('   :', error);
      throw error;
    }
  },

  //   
  async uploadProfileImage(file) {
    try {
      console.log('  :', file);
      const fileExt = file.name.split('.').pop();
      const fileName = `${Date.now()}.${fileExt}`;
      const filePath = `public/${fileName}`;

      const { error: uploadError } = await supabase.storage
        .from('profile_images')
        .upload(filePath, file);

      if (uploadError) {
        console.error('  :', uploadError);
        throw uploadError;
      }

      const { data } = supabase.storage
        .from('profile_images')
        .getPublicUrl(filePath);

      console.log('  URL:', data.publicUrl);
      return data.publicUrl;
    } catch (error) {
      console.error('    :', error);
      throw error;
    }
  },

  //   
  async uploadBlogImage(file) {
    const fileExt = file.name.split('.').pop();
    const fileName = `${Math.random()}.${fileExt}`;
    const filePath = `${fileName}`;

    const { error: uploadError } = await supabase.storage
      .from('blog_images')
      .upload(filePath, file);

    if (uploadError) throw uploadError;

    const { data } = supabase.storage
      .from('blog_images')
      .getPublicUrl(filePath);

    return data.publicUrl;
  },

  //   ( )
  async createPost(postData, imageFile) {
    let imageUrl = null;
    if (imageFile) {
      imageUrl = await this.uploadBlogImage(imageFile);
    }

    const { error } = await supabase.from('posts').insert({
      ...postData,
      image_url: imageUrl,
      created_at: new Date()
    });

    if (error) throw error;
  },

  async getPostNavigation(postId) {
    const { data, error } = await supabase
      .from('posts')
      .select('id, title')
      .or(`id.lt.${postId},id.gt.${postId}`)
      .order('id', { ascending: true })
      .limit(2);

    if (error) throw error;

    const navigation = {
      prev: data.find(p => p.id < postId) || null,
      next: data.find(p => p.id > postId) || null
    };

    return { data: navigation };
  },

    updatePost: async (postId, postData, imageFile) => {
    try {
      let imageUrl = postData.image_url;
      
      // Upload new image if provided
      if (imageFile) {
        imageUrl = await blogApi.uploadBlogImage(imageFile);
      }

      const { data, error } = await supabase
        .from('posts')
        .update({
          ...postData,
          image_url: imageUrl,
          updated_at: new Date()
        })
        .eq('id', postId)
        .single();
        
      if (error) throw error;
      return data;
    } catch (error) {
      throw error;
    }
  },

  //   API
  calendar: {
    /**
     *   .
     */
    async getSchedules() {
      const { data, error } = await supabase
        .from('schedules')
        .select('*');

      if (error) {
        console.error('Error fetching schedules:', error);
        throw error;
      }
      return data;
    },

    /**
     *   'pending'  .
     * @param {object} eventData - { title, start, end, isAllDay }
     */
    async addSchedule(eventData) {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) throw new Error("User not authenticated.");

      const newSchedule = {
        title: eventData.title,
        start_time: eventData.start,
        end_time: eventData.end,
        is_allday: eventData.isAllDay,
        created_by: user.id,
        status: 'pending',
      };

      const { data, error } = await supabase
        .from('schedules')
        .insert([newSchedule])
        .select()
        .single();

      if (error) {
        console.error('Error adding schedule:', error);
        throw error;
      }
      return data;
    },

    /**
     *   . (/)
     * @param {string|number} scheduleId -   ID
     * @param {string} status -   ('approved'  'rejected')
     */
    async updateScheduleStatus(scheduleId, status) {
      const { data, error } = await supabase
        .from('schedules')
        .update({ status: status })
        .eq('id', scheduleId)
        .select()
        .single();

      if (error) {
        console.error('Error updating schedule status:', error);
        throw error;
      }
      return data;
    }
  }
};